import barTitle from '@/components/basics/bar-title';

import _goods_data from '@/static/data/goods.js';	//虚拟数据
import _tool from '@/static/util/tools.js';	//工具函数
export default {
	components: {
		barTitle,
	},
	data() {
		return {
			bannerCur: 0, bannerList: [], bottomModal: false, modalTitle: '', modalType: 'promotion', selectType: '',
			goodsList: [],
			id:0
		}
	},
	onLoad(options) {
		this.bannerList = _goods_data.bannerListData();
		this.goodsList = _goods_data.goodsList();
		this.id=options.id
	},
	onReady() {
		_tool.setBarColor(true);
		uni.pageScrollTo({
		    scrollTop: 0,
		    duration: 0
		});
	},
	methods: {
		bannerSwiper(e) {
			this.bannerCur = e.detail.current;
		},
		serviceTap() {
			this.modalTitle = "服务";
			this.modalType = 'service';
			this.showModal();
		},
		promotionTap() {
			this.modalTitle = "促销优惠";
			this.modalType = 'promotion';
			this.showModal();
		},
		selectTap(type) {
			this.selectType = type;
			this.modalTitle = "选择规格";
			this.modalType = 'select';
			this.showModal();
		},
		showModal() {
			this.bottomModal = true;
		},
		hideModal(e) {
			this.bottomModal = false;
			this.modalTitle = "";
			this.modalType = '';
		},
		myCartTap(){
			uni.navigateTo({
				url: '/pages/goods/my_cart'
			});
		}
	}
}
